import {SET_APP_TIILE, SET_APP_CITY } from '../actions/app.js'

export function AppInfo(state={
  title: "",
  appName: "",
  navBarHidden: false,
  path: "/",
  location: {
    planet: "",
    country: '',
    province: "",
    city: '',
    rectangle: '116.0119343,39.66127144;116.7829835,40.2164962',
    adcode: '110000',
    citySelectModal: false,

  }
},action){
  switch (action.type) {
    case SET_APP_TIILE:
      let title = state.title;
      let navBarHidden = false;
      if (action.path ==='/') {
        title = "";
        document.title = title;
      }
      if (action.path === '/messages') {
        title = "";
        document.title = title;

      }
      if (action.path === '/my') {
        title = "";

        navBarHidden = true;
      }
      return Object.assign({}, state, {
        title,
        navBarHidden,
        path: action.path,
      });
    case SET_APP_CITY:
      return Object.assign({}, state, {
        location: {
          city: action.city,
        }
      });
    default:
      return state;
  }
}



